/* =====================
   BLUR IN (appear effects)
===================== */

// Basic ‚Äì blurred ‚Üí sharp
const blurIn = `
  from { filter: blur(5px); opacity: 0; }
  to { filter: blur(0); opacity: 1; }
`;

// Zoom In ‚Äì scale up slightly while fading in
const blurInZoom = `
  from { opacity: 0; transform: scale(0.9); filter: blur(6px); }
  to { opacity: 1; transform: scale(1); filter: blur(0); }
`;

// Scale Down ‚Äì starts larger, settles to normal
const blurInScale = `
  from { opacity: 0; transform: scale(1.1); filter: blur(6px); }
  to { opacity: 1; transform: scale(1); filter: blur(0); }
`;

// Move Up ‚Äì slide from bottom + sharpen
const blurInUp = `
  from { opacity: 0; transform: translateY(40px); filter: blur(6px); }
  to { opacity: 1; transform: translateY(0); filter: blur(0); }
`;

// Rotate ‚Äì rotate slightly while appearing
const blurInRotate = `
  from { opacity: 0; transform: rotate(-10deg) scale(0.95); filter: blur(6px); }
  to { opacity: 1; transform: rotate(0deg) scale(1); filter: blur(0); }
`;

/* =====================
   BLUR OUT (disappear effects)
===================== */

// Basic ‚Äì sharp ‚Üí blurred
const blurOut = `
  from { filter: blur(0); opacity: 1; }
  to { filter: blur(6px); opacity: 0; }
`;

// Zoom Out ‚Äì scale down while fading out
const blurOutZoom = `
  from { opacity: 1; transform: scale(1); filter: blur(0); }
  to { opacity: 0; transform: scale(0.9); filter: blur(6px); }
`;

// Scale Up ‚Äì grows slightly while vanishing
const blurOutScale = `
  from { opacity: 1; transform: scale(1); filter: blur(0); }
  to { opacity: 0; transform: scale(1.1); filter: blur(6px); }
`;

// Move Down ‚Äì slide down + blur out
const blurOutDown = `
  from { opacity: 1; transform: translateY(0); filter: blur(0); }
  to { opacity: 0; transform: translateY(40px); filter: blur(6px); }
`;

// Rotate ‚Äì rotate slightly while fading out
const blurOutRotate = `
  from { opacity: 1; transform: rotate(0deg) scale(1); filter: blur(0); }
  to { opacity: 0; transform: rotate(10deg) scale(0.95); filter: blur(6px); }
`;

export const blurMap = {
  // In
  blurIn,
  blurInZoom,
  blurInScale,
  blurInUp,
  blurInRotate,

  // Out
  blurOut,
  blurOutZoom,
  blurOutScale,
  blurOutDown,
  blurOutRotate,
};
