import React from 'react';

import { defaultDurationMap } from '../animation/durations';
import type { IFAnimateProps } from '../animation/types';
import { AnimName } from '../key-frames';

import { Animate } from './Animate';

const defaults = {
  delay: '0s',
  direction: 'normal',
  timing: 'ease',
  iteration: 1,
  fillMode: 'forwards',
} as const;

const makeParams = (
  type?: AnimName,
  duration?: string | number,
): Omit<IFAnimateProps, 'children'> => ({
  ...defaults,
  type,
  // @ts-ignore
  duration: duration ?? defaultDurationMap[type ?? 'blurIn'] ?? '1s',
});

export const WrapperAnimate: React.FC<IFAnimateProps> = ({
  children,
  tagName,
  type,
  duration,
  ...props
}) => (
  <Animate {...makeParams(type, duration)} tagName={tagName} {...props}>
    {children}
  </Animate>
);
