import { useRef, useCallback } from "react";
import type { AnimName } from "../key-frames";

import { useAnimate } from "./useAnimate";

export type RecipeState =
  | "idle"
  | "loading"
  | "success"
  | "error"
  | "warning";

const DEFAULT = {
  idle: "fadeIn",
  loading: "float",
  success: "pulse",
  error: "shakeMix",
  warning: "flash",
} as const;

export function useRecipe(options?: {
  map?: Partial<Record<RecipeState, AnimName>>;
  autoReset?: boolean;
  resetDelay?: number;
}) {
  const {
    map = DEFAULT,
    autoReset = true,
    resetDelay = 600,
  } = options || {};

  const anim = useAnimate<HTMLElement>();
  const timer = useRef<number | undefined>();

  const play = useCallback(
    (state: RecipeState) => {
      const name = map[state];
      if (!name) return;

      anim.play(name);

      if (autoReset && state !== "loading") {
        clearTimeout(timer.current);
        timer.current = window.setTimeout(
          anim.reset,
          resetDelay
        );
      }
    },
    [map, autoReset, resetDelay, anim]
  );

  return {
    ...anim,
    play,
  };
}
