import { TypeRunFn } from "../hooks";
import type { AnimName } from "../key-frames";
import type { IFAnimateRecipeOptions } from "./types";

function wait(ms: number) {
  return new Promise<void>((r) => setTimeout(r, ms));
}

function nextFrame() {
  return new Promise<void>((r) =>
    requestAnimationFrame(() => r())
  );
}

export class AnimateSequence {
  private run: TypeRunFn;
  private chain: Promise<void> = Promise.resolve();
  private currentVariant?: string;

  constructor(run: TypeRunFn) {
    this.run = run;
  }

  variant(name: string) {
    this.currentVariant = name;
    return this;
  }

  animate(name: AnimName, options?: IFAnimateRecipeOptions) {
    this.chain = this.chain
      .then(nextFrame)
      .then(() => {
        this.run(name, options);
        return wait(
          typeof options?.duration === 'number'
            ? options.duration
            : 300
        );
      });

    return this;
  }

  wait(ms: number) {
    this.chain = this.chain.then(() => wait(ms));
    return this;
  }

  parallel(
    steps: ((
      api: {
        animate: (
          name: AnimName,
          options?: IFAnimateRecipeOptions
        ) => void;
      }
    ) => void)[],
    waitAfter = 0
  ) {
    this.chain = this.chain
      .then(nextFrame)
      .then(() => {
        const tasks = steps.map(
          (fn) =>
            new Promise<void>((resolve) => {
              fn({
                animate: (name, options) => {
                  this.run(
                    name,
                    options,
                    this.currentVariant
                  );
                  setTimeout(
                    resolve,
                    // @ts-ignore
                    options?.duration ?? 300
                  );
                },
              });
            })
        );

        return Promise.all(tasks).then(() =>
          wait(waitAfter)
        );
      });

    return this;
  }
}
