import type { AnimName } from "../key-frames";
import { useAnimate } from "./useAnimate";

export function useHoverAnimate(options: {
  enter?: AnimName;
  leave?: AnimName;
}) {
  const anim = useAnimate<HTMLElement>();

  return {
    ...anim,
    bind: {
      ref: anim.ref,
      onMouseEnter: () => options.enter && anim.play(options.enter),
      onMouseLeave: () => options.leave && anim.play(options.leave),
    },
  };
}
