// Basic pulse (scale up)
const pulse = `
  from { transform: scale(1); }
  to { transform: scale(1.1); }
`;

// Pulse in & out (scale small -> big -> small)
const pulseInOut = `
  0% { transform: scale(1); }
  50% { transform: scale(1.15); }
  100% { transform: scale(1); }
`;

// Pulse with opacity (fade effect)
const pulseFade = `
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.2); opacity: 0.6; }
`;

// Pulse grow shrink faster
const pulseFast = `
  0%, 100% { transform: scale(1); }
  25% { transform: scale(1.2); }
  75% { transform: scale(0.9); }
`;

// Pulse with color change
const pulseColor = `
  0%, 100% { transform: scale(1); background-color: #ff69b4; }
  50% { transform: scale(1.2); background-color: #ff1493; }
`;

export const pulseMap = {
  pulse,
  pulseInOut,
  pulseFade,
  pulseFast,
  pulseColor,
};
