// hooks/useAnimate.ts
import { useRef, useCallback } from "react";
import { animate } from "../core/createAnimate";
import type { AnimName } from "../key-frames";
import { AnimateSequence, IFAnimateRecipeOptions } from "../animation";

export function useAnimate<T extends HTMLElement>() {
  const ref = useRef<T | null>(null);

  const run = useCallback(
    (name: AnimName, opts?: IFAnimateRecipeOptions) => {
      if (!ref.current) return;
      animate(ref.current).run(name, opts);
    },
    []
  );

  const play = useCallback(
    (name: AnimName, opts?: IFAnimateRecipeOptions) => {
      run(name, opts);
    },
    [run]
  );

  const reset = useCallback(() => {
    if (!ref.current) return;
    animate(ref.current).stop();
  }, []);

  const sequence = useCallback(() => {
    return new AnimateSequence(run);
  }, [run]);

  return {
    ref,
    run,
    play,
    reset,
    sequence,
  };
}
