// Gentle up-down float
const float = `
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
`;

// Sway float with rotation
const floatSway = `
  0% { transform: translate(2%, -10%) rotate(-1deg); }
  100% { transform: translate(-2%, 5%) rotate(3deg); }
`;

// Horizontal float (side to side)
const floatHorizontal = `
  0%, 100% { transform: translateX(0); }
  50% { transform: translateX(12px); }
`;

// Circular float (orbit-like movement)
const floatCircular = `
  0% { transform: translate(0, 0); }
  25% { transform: translate(8px, -8px); }
  50% { transform: translate(0, -12px); }
  75% { transform: translate(-8px, -8px); }
  100% { transform: translate(0, 0); }
`;

// Wiggle float (small tilt oscillation)
const floatWiggle = `
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(2deg); }
  50% { transform: rotate(-2deg); }
  75% { transform: rotate(1deg); }
`;

// Float with scale pulse
const floatPulse = `
  0%, 100% { transform: translateY(0) scale(1); }
  50% { transform: translateY(-8px) scale(1.05); }
`;

// Float diagonal (‚Üó ‚Üô)
const floatDiagonal = `
  0%, 100% { transform: translate(0, 0); }
  50% { transform: translate(10px, -10px); }
`;

// Float swing (like pendulum)
const floatSwing = `
  0%, 100% { transform: rotate(0deg) translateY(0); }
  25% { transform: rotate(3deg) translateY(-5px); }
  50% { transform: rotate(-3deg) translateY(-10px); }
  75% { transform: rotate(2deg) translateY(-5px); }
`;

// Float wave (sinusoidal left-right + up-down)
const floatWave = `
  0% { transform: translate(0, 0); }
  25% { transform: translate(8px, -6px); }
  50% { transform: translate(0, -12px); }
  75% { transform: translate(-8px, -6px); }
  100% { transform: translate(0, 0); }
`;

// Float drift (slow random drift feeling)
const floatDrift = `
  0% { transform: translate(0, 0) rotate(0deg); }
  25% { transform: translate(6px, -4px) rotate(1deg); }
  50% { transform: translate(-4px, -8px) rotate(-1deg); }
  75% { transform: translate(-6px, 6px) rotate(2deg); }
  100% { transform: translate(0, 0) rotate(0deg); }
`;

// Export map for easy use
export const floatMap = {
  float,
  floatSway,
  floatHorizontal,
  floatCircular,
  floatWiggle,

  // Extra
  floatPulse,
  floatDiagonal,
  floatSwing,
  floatWave,
  floatDrift,
};
