// core/injectKeyframe.ts
const injected = new Set<string>();

export function injectKeyframe(
  name: string,
  frames: string
) {
  if (injected.has(name)) return;

  injected.add(name);

  const style = document.createElement("style");
  style.setAttribute("data-az-keyframe", name);

  style.innerHTML = `
@keyframes ${name} {
${frames}
}
  `.trim();

  document.head.appendChild(style);
}
