// === Jelly Animations ===

// Default jelly wobble (scale X/Y alternating)
const jelly = `
  0%, 100% { transform: scale(1, 1); }
  25% { transform: scale(0.9, 1.1); }
  50% { transform: scale(1.1, 0.9); }
  75% { transform: scale(0.95, 1.05); }
`;

// Horizontal rubber stretch
const jellyX = `
  0% { transform: scaleX(1); }
  20% { transform: scaleX(0.9); }
  50% { transform: scaleX(1.25); }
  85% { transform: scaleX(0.8); }
  100% { transform: scaleX(1); }
`;

// Vertical stretch
const jellyY = `
  0% { transform: scaleY(1); }
  20% { transform: scaleY(0.9); }
  50% { transform: scaleY(1.25); }
  85% { transform: scaleY(0.8); }
  100% { transform: scaleY(1); }
`;

// Elastic bounce in (squash + overshoot)
const jellyIn = `
  0% { transform: scale(0.5); opacity: 0; }
  50% { transform: scale(1.2); opacity: 1; }
  70% { transform: scale(0.9); }
  100% { transform: scale(1); }
`;

// Elastic bounce out
const jellyOut = `
  0% { transform: scale(1); opacity: 1; }
  20% { transform: scale(1.1); }
  50% { transform: scale(0.5); opacity: 0.5; }
  100% { transform: scale(0); opacity: 0; }
`;

// === Map ===
export const jellyMap = {
  jelly,
  jellyX,
  jellyY,
  jellyIn,
  jellyOut,
};
