import { useCallback } from "react";
import { useAnimate } from "./useAnimate";
import { AnimName } from "../key-frames";

export function usePairedAnimate<T extends HTMLElement>(options: {
  enter: AnimName;
  leave: AnimName;
  visibleDuration?: number;
}) {
  const { ref, sequence, play } = useAnimate<T>();

  const show = useCallback(() => {
    sequence()
      .animate(options.enter)
      .wait(options.visibleDuration ?? 1000)
      .animate(options.leave);
  }, [sequence, options]);

  return { ref, show, play };
}
