/* -------------------------------
   Fold / Unfold Animations Map
---------------------------------*/

// Simple version (scaleY)
const fold = `from { transform: scaleY(1); } to { transform: scaleY(0); }`;
const unfold = `from { transform: scaleY(0); } to { transform: scaleY(1); }`;

// Advanced version (3D + opacity + multi-step)
const foldDeep = `
  0% { transform: scale3d(1, 1, 1); }
  30% { transform: scale3d(1, 0.4, 1); }
  60% { transform: scale3d(0.4, 0.4, 1); }
  100% { opacity: 0; transform: scale3d(0.2, 0.2, 0.2); }
`;

const unfoldDeep = `
  0% { opacity: 0; transform: scale3d(0, 0, 0); }
  30% { opacity: 1; transform: scale3d(0.4, 0.4, 1); }
  60% { transform: scale3d(0.4, 1, 1); }
  100% { transform: scale3d(1, 1, 1); }
`;

// Fold Left (rotate around Y)
const foldLeft = `
  from { transform: perspective(400px) rotateY(0); opacity: 1; }
  to { transform: perspective(400px) rotateY(-90deg); opacity: 0; }
`;

// Unfold Left
const unfoldLeft = `
  from { transform: perspective(400px) rotateY(-90deg); opacity: 0; }
  to { transform: perspective(400px) rotateY(0); opacity: 1; }
`;

// Fold Right (rotate around Y opposite)
const foldRight = `
  from { transform: perspective(400px) rotateY(0); opacity: 1; }
  to { transform: perspective(400px) rotateY(90deg); opacity: 0; }
`;

// Unfold Right
const unfoldRight = `
  from { transform: perspective(400px) rotateY(90deg); opacity: 0; }
  to { transform: perspective(400px) rotateY(0); opacity: 1; }
`;

// Fold Up (rotate around X)
const foldUp = `
  from { transform: perspective(400px) rotateX(0); opacity: 1; }
  to { transform: perspective(400px) rotateX(-90deg); opacity: 0; }
`;

// Unfold Up
const unfoldUp = `
  from { transform: perspective(400px) rotateX(-90deg); opacity: 0; }
  to { transform: perspective(400px) rotateX(0); opacity: 1; }
`;

// Fold Down
const foldDown = `
  from { transform: perspective(400px) rotateX(0); opacity: 1; }
  to { transform: perspective(400px) rotateX(90deg); opacity: 0; }
`;

// Unfold Down
const unfoldDown = `
  from { transform: perspective(400px) rotateX(90deg); opacity: 0; }
  to { transform: perspective(400px) rotateX(0); opacity: 1; }
`;

// Map for lookup
export const foldMap = {
  fold,
  unfold,
  foldDeep,
  unfoldDeep,

  // Extra
  foldLeft,
  unfoldLeft,
  foldRight,
  unfoldRight,
  foldUp,
  unfoldUp,
  foldDown,
  unfoldDown,
};
