import { useCallback, useRef } from "react";
import type { AnimName } from "../key-frames";
import type { IFAnimateRecipeOptions } from "../animation/types";

import { TypeRunFn } from "./types";

export function useRandomAnimateNoRepeat(
  run: TypeRunFn,
  pool: readonly AnimName[]
) {
  const lastRef = useRef<AnimName | null>(null);

  return useCallback(
    (options?: IFAnimateRecipeOptions) => {
      if (!pool.length) return;

      let next: AnimName;
      do {
        next = pool[Math.floor(Math.random() * pool.length)];
      } while (pool.length > 1 && next === lastRef.current);

      lastRef.current = next;
      run(next, options);
    },
    [run, pool]
  );
}
