import { attentionMap } from './attention';
import { blurMap } from './blur';
import { bounceMap } from './bounce';
import { fadeMap } from './fade';
import { flashMap } from './flash';
import { flipMap } from './flip';
import { floatMap } from './float';
import { foldMap } from './fold';
import { funMap } from './funMap';
import { glowMap } from './glowing';
import { hangOnMap } from './hangOn';
import { jellyMap } from './jelly';
import { motionMap } from './motion';
import { popMap } from './pop';
import { pulseMap } from './pulse';
import { rotateMap } from './rotate';
import { slideMap } from './slide';
import { squeezeShakeMap } from './squeezeShake';
import { textEffectsMap } from './textEffects';
import { zoomMap } from './zoom';

/* =========================
   Frames Map (internal)
========================= */
export const animFramesMaps = {
  ...bounceMap,
  ...textEffectsMap,
  ...blurMap,
  ...flashMap,
  ...floatMap,
  ...glowMap,
  ...jellyMap,
  ...motionMap,
  ...pulseMap,
  ...fadeMap,
  ...squeezeShakeMap,
  ...slideMap,
  ...flipMap,
  ...foldMap,
  ...hangOnMap,
  ...zoomMap,
  ...rotateMap,
  ...funMap,
  ...popMap,
  ...attentionMap,
} as const;

/* =========================
   Public Types
========================= */
export type AnimName = keyof typeof animFramesMaps;

/* =========================
   Names
========================= */
export const animNames = Object.keys(animFramesMaps) as AnimName[];

/* =========================
   Timing
========================= */
export const timingMap = {
  linear: 'linear',
  ease: 'ease',
  easeIn: 'ease-in',
  'ease-in': 'ease-in',
  easeOut: 'ease-out',
  'ease-out': 'ease-out',
  easeInOut: 'ease-in-out',
  'ease-in-out': 'ease-in-out',
} as const;

export type TypeTimingKey = keyof typeof timingMap;

/* =========================
   Groups
========================= */
const groupConfig = [
  { name: 'üéæ Bounce', key: 'bounce', prefix: 'bounce' },
  { name: '‚ú® Text / Glow Effects', key: 'text', prefix: 'effect' },
  { name: 'üå´ Blur', key: 'blur', prefix: 'blur' },
  { name: '‚ö° Flash', key: 'flash', prefix: 'flash' },
  { name: 'üéà Float', key: 'float', prefix: 'float' },
  { name: 'üí° Glow', key: 'glow', prefix: 'glow' },
  { name: 'üçÆ Jelly', key: 'jelly', prefix: 'jelly' },
  { name: 'üåë Shadow / Spin / Swing / Orbit', key: 'motion', prefix: '' },
  { name: 'üíì Pulse', key: 'pulse', prefix: 'pulse' },
  { name: 'üå´ Fade', key: 'fade', prefix: 'fade' },
  { name: 'ü§Ø Squeeze / Shake', key: 'shake', prefix: 'shake' },
  { name: 'üì• Slide', key: 'slide', prefix: 'slide' },
  { name: 'üîÑ Flip', key: 'flip', prefix: 'flip' },
  { name: 'üìÇ Fold / Unfold', key: 'fold', prefix: 'fold' },
  { name: 'ü™ù Hang On', key: 'hangOn', prefix: 'hangOn' },
  { name: 'üîç Zoom', key: 'zoom', prefix: 'zoom' },
  { name: 'üåÄ Rotate', key: 'rotate', prefix: 'rotate' },
  { name: 'üéâ Fun / Attention', key: 'fun', prefix: '' },
] as const;

type GroupKey = (typeof groupConfig)[number]['key'];

export const animGroups: Record<GroupKey, AnimName[]> =
  Object.fromEntries(
    groupConfig.map(({ key, prefix }) => {
      const items = animNames.filter((name) =>
        prefix ? name.startsWith(prefix) : true
      );
      return [key, items];
    })
  ) as Record<GroupKey, AnimName[]>;
