import React, { forwardRef } from "react";
import { defaultDurationMap } from "../animation/durations";
import type { IFAnimateProps } from "../animation/types";
import { StyledElement } from "./AnimateStyled";

export const Animate = forwardRef<
  HTMLElement,
  IFAnimateProps
>(function Animate(
  {
    tagName: Tag = "div",
    children,
    className,
    type = "blurIn",
    duration,
    timing = "ease",
    delay = "0s",
    direction = "alternate",
    fillMode = "forwards",
    iteration,
    ...props
  },
  ref
) {
  const resolved =
    // @ts-ignore
    duration ?? defaultDurationMap[type] ?? "1s";

  const durationVal =
    typeof resolved === "number"
      ? `${resolved}ms`
      : resolved;

  return (
    <StyledElement
      as={Tag as any}
      ref={ref as any}
      className={className}
      type={type}
      duration={durationVal}
      timing={timing}
      delay={delay}
      iteration={iteration ?? 1}
      direction={direction}
      fillMode={fillMode}
      {...props}
    >
      {children}
    </StyledElement>
  );
});

export default Animate;
