// components/AnimateOn.tsx
import React, {
  useEffect,
  useRef,
  ReactElement,
  cloneElement,
} from "react";
import { useAnimate } from "../hooks/useAnimate";
import type { IFAnimateRecipeOptions } from "../animation/types";

import type { AnimName } from "../key-frames";

export interface IFAnimateOnProps<T = any> {
  when: T;
  value?: T;
  anim: AnimName | AnimName[];
  options?: IFAnimateRecipeOptions;
  children: ReactElement;
}

export function AnimateOn<T>({
  when,
  value,
  anim,
  options,
  children,
}: IFAnimateOnProps<T>) {
  const animApi = useAnimate<HTMLElement>();
  const prev = useRef<T>(when);

  useEffect(() => {
    let shouldPlay = false;

    if (value === undefined) {
      shouldPlay = when !== prev.current;
    } else {
      // trigger khi when ch·∫°m value
      shouldPlay =
        when === value && prev.current !== value;
    }

    if (shouldPlay) {
      if (Array.isArray(anim)) {
        const seq = animApi.sequence();
        anim.forEach((name) => {
          seq.animate(name, options);
        });
      } else {
        animApi.play(anim, options);
      }
    }

    prev.current = when;
  }, [when, value, anim, options, animApi]);

  return cloneElement(children, {
    ref: (node: HTMLElement | null) => {
      animApi.ref.current = node;

      const childRef = (children as any).ref;
      if (typeof childRef === "function") {
        childRef(node);
      } else if (childRef && "current" in childRef) {
        childRef.current = node;
      }
    },
  });
}

export default AnimateOn;
