// hooks/useAnimateSequence.ts
import { useCallback, useMemo } from "react";
import { animate } from "../core/createAnimate";
import { AnimateSequence, IFAnimateRecipeOptions } from "../animation";
import { AnimName } from "../key-frames";

export function useAnimateSequence<T extends HTMLElement>(
  ref: React.RefObject<T>
) {
  const run = useCallback(
    (name: AnimName, opts?: IFAnimateRecipeOptions) => {
      if (!ref.current) return;
      animate(ref.current).run(name, opts);
    },
    [ref]
  );

  return useMemo(
    () => new AnimateSequence(run),
    [run]
  );
}
