import { useCallback, useMemo } from "react";

import type { AnimName } from "../key-frames";
import type { IFAnimateRecipeOptions } from "../animation/types";
import type { TypeRunFn } from "./types";

interface IFWeightedAnim {
  name: AnimName;
  weight: number;
}

export function useWeightedRandomAnimate(
  run: TypeRunFn,
  pool: readonly IFWeightedAnim[]
) {
  const totalWeight = useMemo(
    () => pool.reduce((s, p) => s + Math.max(0, p.weight), 0),
    [pool]
  );

  return useCallback(
    (options?: IFAnimateRecipeOptions) => {
      if (!pool.length || totalWeight <= 0) return;

      let r = Math.random() * totalWeight;

      for (const item of pool) {
        const w = Math.max(0, item.weight);
        r -= w;
        if (r <= 0) {
          run(item.name, options);
          return;
        }
      }
    },
    [run, pool, totalWeight]
  );
}
