import React, {
  useEffect,
  useRef,
  useState,
  ReactElement,
  cloneElement,
} from "react";

import type { AnimName } from "../key-frames";
import { useAnimate } from "../hooks/useAnimate";

export interface IFAnimatePresenceProps {
  /** show / hide */
  show: boolean;
  enter: AnimName;
  exit: AnimName;
  duration?: number;
  children: ReactElement;
}

export const AnimatePresence: React.FC<IFAnimatePresenceProps> = ({
  show,
  enter,
  exit,
  duration = 300,
  children,
}) => {
  const anim = useAnimate<HTMLElement>();
  const [mounted, setMounted] = useState(show);
  const prev = useRef(show);

  useEffect(() => {
    // ---- ENTER ----
    if (show && !prev.current) {
      setMounted(true);
    }

    // ---- EXIT ----
    if (!show && prev.current) {
      anim.play(exit);

      const t = setTimeout(() => {
        setMounted(false);
      }, duration);

      return () => clearTimeout(t);
    }

    prev.current = show;
  }, [show, enter, exit, duration]);

  useEffect(() => {
    if (mounted && show) {
      anim.play(enter);
    }
  }, [mounted, show, enter]);

  if (!mounted) return null;

  return cloneElement(children, {
    ref: (node: any) => {
      anim.ref.current = node;

      const childRef = (children as any).ref;
      if (typeof childRef === "function") {
        childRef(node);
      } else if (childRef) {
        childRef.current = node;
      }
    },
  });
}

export default AnimatePresence;
