import { useCallback } from "react";
import type { AnimName } from "../key-frames";
import type { IFAnimateRecipeOptions } from "../animation/types";

export function useRandomAnimate(
  run: (name: AnimName, o?: IFAnimateRecipeOptions) => void,
  pool: readonly AnimName[]
) {
  return useCallback(
    (options?: IFAnimateRecipeOptions) => {
      if (!pool.length) return;
      const name =
        pool[Math.floor(Math.random() * pool.length)];
      run(name, options);
    },
    [run, pool]
  );
}
