/* ===========================================================
   Slide In / Out - Linear
=========================================================== */
const slideInFromLeft = `
  from { transform: translateX(-100%); opacity: 0; }
  to   { transform: translateX(0); opacity: 1; }
`;

const slideInFromRight = `
  from { transform: translateX(100%); opacity: 0; }
  to   { transform: translateX(0); opacity: 1; }
`;

const slideOutToLeft = `
  from { transform: translateX(0); opacity: 1; }
  to   { transform: translateX(-100%); opacity: 0; }
`;

const slideOutToRight = `
  from { transform: translateX(0); opacity: 1; }
  to   { transform: translateX(100%); opacity: 0; }
`;

const slideInFromTop = `
  from { transform: translateY(-100%); opacity: 0; }
  to   { transform: translateY(0); opacity: 1; }
`;

const slideInFromBottom = `
  from { transform: translateY(100%); opacity: 0; }
  to   { transform: translateY(0); opacity: 1; }
`;

const slideOutToTop = `
  from { transform: translateY(0); opacity: 1; }
  to   { transform: translateY(-100%); opacity: 0; }
`;

const slideOutToBottom = `
  from { transform: translateY(0); opacity: 1; }
  to   { transform: translateY(100%); opacity: 0; }
`;

/* ===========================================================
   Slide In / Out - Overshoot (bouncy effect)
=========================================================== */
const slideInFromLeftOvershoot = `
  0%   { transform: translateX(-100%); opacity: 0; }
  60%  { transform: translateX(20%);  opacity: 1; }
  100% { transform: translateX(0);    opacity: 1; }
`;

const slideInFromRightOvershoot = `
  0%   { transform: translateX(100%); opacity: 0; }
  60%  { transform: translateX(-20%); opacity: 1; }
  100% { transform: translateX(0);    opacity: 1; }
`;

const slideOutToLeftOvershoot = `
  0%   { transform: translateX(0);     opacity: 1; }
  60%  { transform: translateX(-120%); opacity: 0; }
  100% { transform: translateX(-100%); opacity: 0; }
`;

const slideOutToRightOvershoot = `
  0%   { transform: translateX(0);    opacity: 1; }
  60%  { transform: translateX(120%); opacity: 0; }
  100% { transform: translateX(100%); opacity: 0; }
`;

const slideInFromTopOvershoot = `
  0%   { transform: translateY(-100%); opacity: 0; }
  60%  { transform: translateY(20%);   opacity: 1; }
  100% { transform: translateY(0);     opacity: 1; }
`;

const slideInFromBottomOvershoot = `
  0%   { transform: translateY(100%); opacity: 0; }
  60%  { transform: translateY(-20%); opacity: 1; }
  100% { transform: translateY(0);    opacity: 1; }
`;

const slideOutToTopOvershoot = `
  0%   { transform: translateY(0);      opacity: 1; }
  60%  { transform: translateY(-120%); opacity: 0; }
  100% { transform: translateY(-100%); opacity: 0; }
`;

const slideOutToBottomOvershoot = `
  0%   { transform: translateY(0);     opacity: 1; }
  60%  { transform: translateY(120%); opacity: 0; }
  100% { transform: translateY(100%); opacity: 0; }
`;

/* ===========================================================
   Map Export
=========================================================== */
export const slideMap = {
  // Linear
  slideInFromLeft,
  slideInFromRight,
  slideOutToLeft,
  slideOutToRight,
  slideInFromTop,
  slideInFromBottom,
  slideOutToTop,
  slideOutToBottom,

  // Overshoot / Bouncy
  slideInFromLeftOvershoot,
  slideInFromRightOvershoot,
  slideOutToLeftOvershoot,
  slideOutToRightOvershoot,
  slideInFromTopOvershoot,
  slideInFromBottomOvershoot,
  slideOutToTopOvershoot,
  slideOutToBottomOvershoot,
};
