import { useEffect, useState } from 'react';

interface IFUseTypingIFAnimateOptions {
  text: string;
  speed?: number;
  loop?: boolean;
}

export function useTypingAnimate({
  text,
  speed = 50,
  loop = false,
}: IFUseTypingIFAnimateOptions) {
  const [output, setOutput] = useState('');
  const [index, setIndex] = useState(0);
  const [done, setDone] = useState(false);

  useEffect(() => {
    if (index >= text.length) {
      setDone(true);

      if (!loop) return;

      const t = setTimeout(() => {
        setIndex(0);
        setOutput('');
        setDone(false);
      }, 800);

      return () => clearTimeout(t);
    }

    const t = setTimeout(() => {
      setOutput((s) => s + text[index]);
      setIndex((i) => i + 1);
    }, speed);

    return () => clearTimeout(t);
  }, [index, text, speed, loop]);

  return {
    output,
    isDone: done,
    reset: () => {
      setIndex(0);
      setOutput('');
      setDone(false);
    },
  };
}
