import { useTypingAnimate } from "../hooks";

interface TypingTextProps {
  text: string;
  speed?: number;
  loop?: boolean;
  as?: keyof JSX.IntrinsicElements;
}

export function TypingText({
  text,
  speed,
  loop,
  as: Tag = 'span',
}: TypingTextProps) {
  const { output } = useTypingAnimate({ text, speed, loop });
  return <Tag>{output}</Tag>;
}
