import { useEffect, useRef } from "react";
import type { AnimName } from "../key-frames";
import { useAnimate } from "./useAnimate";

export function useAutoAnimate<T extends HTMLElement>(
  value: any,
  options: { change: AnimName }
) {
  const anim = useAnimate<T>();
  const prev = useRef(value);

  useEffect(() => {
    if (prev.current !== value) {
      anim.play(options.change);
      prev.current = value;
    }
  }, [value, options.change, anim]);

  return anim;
}
