/** 1. 3D Depth (stacked shadow) */
const effect3D = `
  to {
    text-shadow:
      0 1px 0 #ccc,
      0 2px 0 #c9c9c9,
      0 3px 0 #bbb,
      0 4px 0 #b9b9b9,
      0 5px 0 #aaa,
      0 6px 1px rgba(0, 0, 0, .1),
      0 0 5px rgba(0, 0, 0, .1),
      0 1px 3px rgba(0, 0, 0, .3),
      0 3px 5px rgba(0, 0, 0, .2),
      0 5px 10px rgba(0, 0, 0, .25);
  }
`;

/** 2. Neon Glow */
const neonGlow = `
  from { text-shadow: none; }
  to {
    text-shadow:
      0 0 5px #0ff,
      0 0 10px #0ff,
      0 0 20px #0ff,
      0 0 40px #0ff,
      0 0 80px #0ff;
    color: #fff;
  }
`;

/** 3. Retro 3D */
const retro3D = `
  to {
    text-shadow:
      2px 2px 0 #ff0040,
      4px 4px 0 #00ffd5,
      6px 6px 0 #000;
  }
`;

/** 4. Emboss */
const emboss = `
  to {
    text-shadow:
      -1px -1px 1px #fff,
      1px 1px 2px rgba(0,0,0,0.6);
    color: #555;
  }
`;

/** 5. Fire Glow */
const fireGlow = `
  0%, 100% {
    text-shadow:
      0 0 5px #ff6600,
      0 0 10px #ff3300,
      0 0 20px #ff0000;
  }
  50% {
    text-shadow:
      0 0 10px #ff9933,
      0 0 20px #ff6600,
      0 0 30px #ff3300;
  }
`;

/** 6. Ice Glow */
const iceGlow = `
  to {
    text-shadow:
      0 0 5px #66ccff,
      0 0 10px #33ccff,
      0 0 20px #00aaff,
      0 0 40px #0099ff;
    color: #e6f9ff;
  }
`;

/* 7. Shine */
const shine = `
  0% { background-position: -200%; }
  100% { background-position: 200%; }
`;

export const textEffectsMap = {
  effect3D,
  neonGlow,
  retro3D,
  emboss,
  fireGlow,
  iceGlow,
  shine,
};
