import { useAnimate } from "./useAnimate";

const MAP = {
  success: "pulse",
  error: "shakeMix",
  loading: "float",
  warning: "flash",
} as const;

export function useAttention<T extends HTMLElement>() {
  const anim = useAnimate<T>();

  return {
    ...anim,
    success: () => anim.play(MAP.success),
    error: () => anim.play(MAP.error),
    loading: () => anim.play(MAP.loading),
    warning: () => anim.play(MAP.warning),
  };
}
