// core/createAnimate.ts
import type React from "react";
import { defaultDurationMap } from "../animation/durations";
import type { AnimName } from "../key-frames";
import type { IFAnimateRecipeOptions } from "../animation/types";
import { animFramesMaps } from "../key-frames";

import { injectKeyframe } from "./injectKeyframe";

type Target =
  | HTMLElement
  | null
  | undefined
  | React.RefObject<HTMLElement>;

function resolve(target?: Target): HTMLElement | null {
  if (!target) return null;
  if (typeof target === "object" && "current" in target) {
    return target.current;
  }
  return target;
}

export function animate(target?: Target) {
  const el = resolve(target);
  let cleanup: (() => void) | null = null;

  function clear() {
    if (!el) return;
    el.style.animation = "";
    el.style.removeProperty("animation-name");
    el.style.removeProperty("animation-duration");
    el.style.removeProperty("animation-timing-function");
    el.style.removeProperty("animation-iteration-count");
    el.style.removeProperty("animation-fill-mode");
  }

  function run(
    name: AnimName,
    opts: IFAnimateRecipeOptions = {}
  ) {
    if (!el) return;

    // cancel previous
    if (cleanup) {
      cleanup();
      cleanup = null;
    }

    const frames = animFramesMaps[name];
    if (frames) {
      injectKeyframe(`az-${name}`, frames);
    }

    const duration =
      opts.duration ??
      // @ts-ignore
      defaultDurationMap[name] ??
      "1s";

    // restart animation
    el.style.animation = "none";
    void el.offsetHeight;

    el.style.animationName = `az-${name}`;
    el.style.animationDuration =
      typeof duration === "number"
        ? `${duration}ms`
        : duration;

    el.style.animationTimingFunction =
      opts.timing ?? "ease";

    el.style.animationIterationCount =
      String(opts.iteration ?? 1);

    el.style.animationFillMode = "both";

    if (!opts.keep) {
      const onEnd = (e: AnimationEvent) => {
        if (e.target !== el) return;
        clear();
        el.removeEventListener("animationend", onEnd);
        cleanup = null;
      };

      el.addEventListener("animationend", onEnd);

      cleanup = () => {
        el.removeEventListener("animationend", onEnd);
        clear();
      };
    }
  }

  function stop() {
    if (cleanup) cleanup();
    cleanup = null;
    clear();
  }

  return { run, stop };
}
