// AnimateStyled.tsx
import styled, { css, keyframes } from 'styled-components';
import { timingMap, IFBaseStyledElementProps } from '../animation/types';
import { animFramesMaps } from '../key-frames';

const blockedProps = [
  'type',
  'duration',
  'timing',
  'delay',
  'iteration',
  'direction',
  'fillMode',
  'tagName',
];

const shouldForwardProp = (prop: string) => !blockedProps.includes(prop);

/* =========================
   Keyframes cache (IMPORTANT)
========================= */
const keyframesCache: Record<string, ReturnType<typeof keyframes>> = {};

function getKeyframes(type?: string) {
  if (!type) return undefined;
  if (!keyframesCache[type]) {
    // @ts-ignore
    keyframesCache[type] = keyframes`${animFramesMaps[type]}`;
  }
  return keyframesCache[type];
}

/* =========================
   Styled Element
========================= */
export const StyledElement = styled('div').withConfig({
  shouldForwardProp,
})<IFBaseStyledElementProps>`
  margin: 0;
  padding: 0;

  ${({ type, duration = 1000, timing, delay, iteration, direction, fillMode }) => {
    const frames = getKeyframes(type);

    if (!frames) return '';

    return css`
      animation-name: ${frames};
      animation-duration: ${typeof duration === 'number' ? `${duration}ms` : duration};
      animation-timing-function: ${timing ? timingMap[timing] : 'ease'};
      animation-delay: ${typeof delay === 'number' ? `${delay}ms` : delay || '0ms'};
      animation-iteration-count: ${iteration ?? 1};
      animation-direction: ${direction ?? 'normal'};
      animation-fill-mode: ${fillMode ?? 'both'};
    `;
  }}
`;
