// --- Basic ---
const fadeIn = `
  from { opacity: 0; }
  to { opacity: 1; }
`;
const fadeOut = `
  from { opacity: 1; }
  to { opacity: 0; }
`;

// --- Directional ---
const fadeInFromLeft = `
  from { opacity: 0; transform: translateX(-100%); }
  to { opacity: 1; transform: translateX(0); }
`;
const fadeInFromRight = `
  from { opacity: 0; transform: translateX(100%); }
  to { opacity: 1; transform: translateX(0); }
`;
const fadeInFromTop = `
  from { opacity: 0; transform: translateY(-100%); }
  to { opacity: 1; transform: translateY(0); }
`;
const fadeInFromBottom = `
  from { opacity: 0; transform: translateY(100%); }
  to { opacity: 1; transform: translateY(0); }
`;

const fadeOutToLeft = `
  from { opacity: 1; transform: translateX(0); }
  to { opacity: 0; transform: translateX(-100%); }
`;
const fadeOutToRight = `
  from { opacity: 1; transform: translateX(0); }
  to { opacity: 0; transform: translateX(100%); }
`;
const fadeOutToTop = `
  from { opacity: 1; transform: translateY(0); }
  to { opacity: 0; transform: translateY(-100%); }
`;
const fadeOutToBottom = `
  from { opacity: 1; transform: translateY(0); }
  to { opacity: 0; transform: translateY(100%); }
`;

// --- Scale ---
const fadeInZoom = `
  from { opacity: 0; transform: scale(0.5); }
  to { opacity: 1; transform: scale(1); }
`;
const fadeOutZoom = `
  from { opacity: 1; transform: scale(1); }
  to { opacity: 0; transform: scale(0.5); }
`;

// --- Rotate ---
const fadeInRotate = `
  from { opacity: 0; transform: rotate(-45deg); }
  to { opacity: 1; transform: rotate(0); }
`;
const fadeOutRotate = `
  from { opacity: 1; transform: rotate(0); }
  to { opacity: 0; transform: rotate(45deg); }
`;

// --- Skew ---
const fadeInSkew = `
  from { opacity: 0; transform: skewX(-30deg); }
  to { opacity: 1; transform: skewX(0); }
`;
const fadeOutSkew = `
  from { opacity: 1; transform: skewX(0); }
  to { opacity: 0; transform: skewX(30deg); }
`;

// --- Flip (3D) ---
const fadeInFlipX = `
  from { opacity: 0; transform: rotateX(90deg); }
  to { opacity: 1; transform: rotateX(0); }
`;

const fadeOutFlipX = `
  from { opacity: 1; transform: rotateX(0); }
  to { opacity: 0; transform: rotateX(-90deg); }
`;
const fadeInFlipY = `
  from { opacity: 0; transform: rotateY(90deg); }
  to { opacity: 1; transform: rotateY(0); }
`;
const fadeOutFlipY = `
  from { opacity: 1; transform: rotateY(0); }
  to { opacity: 0; transform: rotateY(-90deg); }
`;

// --- Perspective (3D depth) ---
const fadeInPerspective = `
  from { opacity: 0; transform: perspective(400px) translateZ(-200px); }
  to { opacity: 1; transform: perspective(400px) translateZ(0); }
`;
const fadeOutPerspective = `
  from { opacity: 1; transform: perspective(400px) translateZ(0); }
  to { opacity: 0; transform: perspective(400px) translateZ(-200px); }
`;

// --- Fade with Blur ---
const fadeInBlur = `
  from { opacity: 0; filter: blur(10px); }
  to { opacity: 1; filter: blur(0); }
`;
const fadeOutBlur = `
  from { opacity: 1; filter: blur(0); }
  to { opacity: 0; filter: blur(10px); }
`;

// --- Fade with Color Shift ---
const fadeInColor = `
  from { opacity: 0; filter: hue-rotate(180deg); }
  to { opacity: 1; filter: hue-rotate(0); }
`;
const fadeOutColor = `
  from { opacity: 1; filter: hue-rotate(0); }
  to { opacity: 0; filter: hue-rotate(180deg); }
`;

// --- Fade Pulse (loopable) ---
const fadePulse = `
  0%, 100% { opacity: 0.4; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.05); }
`;

// --- Fade Bounce ---
const fadeBounce = `
  0% { opacity: 0; transform: translateY(20px); }
  50% { opacity: 1; transform: translateY(-10px); }
  70% { transform: translateY(5px); }
  100% { transform: translateY(0); }
`;

// --- Fade Swing (like pendulum) ---
const fadeSwing = `
  0% { opacity: 0; transform: rotate(15deg); }
  50% { opacity: 1; transform: rotate(-15deg); }
  100% { opacity: 1; transform: rotate(0deg); }
`;

// --- Fade Zoom Rotate ---
const fadeZoomRotate = `
  from { opacity: 0; transform: scale(0.5) rotate(-180deg); }
  to { opacity: 1; transform: scale(1) rotate(0); }
`;

// --- Fade Elastic ---
const fadeElastic = `
  0% { opacity: 0; transform: scale(0.5); }
  60% { opacity: 1; transform: scale(1.2); }
  80% { transform: scale(0.9); }
  100% { transform: scale(1); }
`;

// --- Fade Shine (gradient wipe) ---
const fadeShine = `
  0% { opacity: 0; background-position: -200%; }
  100% { opacity: 1; background-position: 200%; }
`;

// ================== Map ==================
export const fadeMap = {
  // Basic
  fadeIn,
  fadeOut,

  // Direction
  fadeInFromLeft,
  fadeInFromRight,
  fadeInFromTop,
  fadeInFromBottom,
  fadeOutToLeft,
  fadeOutToRight,
  fadeOutToTop,
  fadeOutToBottom,

  // Aliases (DX)
  fadeInUp: fadeInFromBottom,
  fadeInDown: fadeInFromTop,
  fadeInLeft: fadeInFromRight,
  fadeInRight: fadeInFromLeft,

  // Scale
  fadeInZoom,
  fadeOutZoom,

  // Rotate
  fadeInRotate,
  fadeOutRotate,

  // Skew
  fadeInSkew,
  fadeOutSkew,

  // Flip
  fadeInFlipX,
  fadeOutFlipX,
  fadeInFlipY,
  fadeOutFlipY,

  // Perspective
  fadeInPerspective,
  fadeOutPerspective,

  // Blur
  fadeInBlur,
  fadeOutBlur,

  // Color
  fadeInColor,
  fadeOutColor,

  // Pulse & Bounce
  fadePulse,
  fadeBounce,

  // Swing
  fadeSwing,

  // Zoom + Rotate
  fadeZoomRotate,

  // Elastic
  fadeElastic,

  // Shine
  fadeShine,
};
