import React, {
  forwardRef,
  ElementType,
} from "react";
import type { IFAnimateProps } from "../animation/types";

import Animate from "./Animate";

export interface IFAnimateHostProps<
  T extends ElementType = "div"
> extends Omit<IFAnimateProps, "children"> {
  as?: T;
  children: React.ReactNode;
}

export const AnimateHost = forwardRef<
  HTMLElement,
  IFAnimateHostProps
>(function AnimateHost(
  { as, children, ...props },
  ref
) {
  return (
    <Animate
      {...props}
      tagName={as}
      ref={ref as any}
    >
      {children}
    </Animate>
  );
});

export default AnimateHost;
