// Classic flash - even blink
const flash = `
  0%, 50%, 100% { opacity: 1; }
  25%, 75% { opacity: 0; }
`;

// Irregular flash - uneven blink
const flashIrregular = `
  0%, 40%, 80% { opacity: 1; }
  20%, 60%, 100% { opacity: 0; }
`;

// Fast flash - quicker blink
const flashFast = `
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
`;

// Slow flash - long fade in/out
const flashSlow = `
  0% { opacity: 1; }
  45% { opacity: 0; }
  55% { opacity: 0; }
  100% { opacity: 1; }
`;

// Pulse flash - smooth opacity transition
const flashPulse = `
  0%, 100% { opacity: 1; }
  50% { opacity: 0.3; }
`;

// Glow flash - opacity + scale
const flashGlow = `
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.4; transform: scale(1.05); }
`;

// Color flash - opacity + hue
const flashColor = `
  0%, 100% { opacity: 1; filter: hue-rotate(0deg); }
  50% { opacity: 0.2; filter: hue-rotate(180deg); }
`;

// Strobe flash - rapid blinking
const flashStrobe = `
  0%, 20%, 40%, 60%, 80%, 100% { opacity: 1; }
  10%, 30%, 50%, 70%, 90% { opacity: 0; }
`;

// Soft flash - gentle fade in/out
const flashSoft = `
  0% { opacity: 1; }
  50% { opacity: 0.6; }
  100% { opacity: 1; }
`;

// Reverse pulse - fade strong then dim
const flashReversePulse = `
  0%, 100% { opacity: 0.3; }
  50% { opacity: 1; }
`;

// Export map for easy use
export const flashMap = {
  flash,
  flashIrregular,
  flashFast,
  flashSlow,
  flashPulse,

  // Extra
  flashGlow,
  flashColor,
  flashStrobe,
  flashSoft,
  flashReversePulse,
};
