import { useMemo, useCallback, useEffect } from 'react';

import type { AnimName } from '../key-frames';
import type { IFAnimateRecipeOptions } from '../animation/types';
import { createRecipe } from '../core/recipe';

import { useAnimate } from './useAnimate';

/* =========================
   Types
========================= */

export interface IFUseAnimateControllerOptions {
  initial?: AnimName;
  recipe?: Partial<
    Record<
      string,
      AnimName | ((o?: IFAnimateRecipeOptions) => AnimName)
    >
  >;
}

/* =========================
   Hook
========================= */

export function useAnimateController<T extends HTMLElement>(
  options: IFUseAnimateControllerOptions = {}
) {
  const anim = useAnimate<T>();

  /* ---------- play with fallback ---------- */
  const play = useCallback(
    (name?: AnimName, opts?: IFAnimateRecipeOptions) => {
      const animName = name ?? options.initial;
      if (!animName) return;
      anim.play(animName, opts);
    },
    [anim.play, options.initial]
  );

  /* ---------- auto initial ---------- */
  useEffect(() => {
    if (options.initial) {
      play(options.initial);
    }
  }, [options.initial, play]);

  /* ---------- recipe ---------- */
  const recipe = useMemo(() => {
    if (!options.recipe) return undefined;
    return createRecipe(anim.run, options.recipe);
  }, [anim.run, options.recipe]);

  return {
    ...anim,

    /* override / extend */
    play,
    recipe,
  };
}

