import { useCallback } from "react";
import type { AnimName } from "../key-frames";
import type {
  IFAnimateRecipeOptions,
  TypeAnimationIntensity,
} from "../animation/types";
import { TypeRunFn } from "./types";

type TypeIntensityPool = Record<
  TypeAnimationIntensity,
  readonly AnimName[]
>;

export function useRandomAnimateByIntensity(
  run: TypeRunFn,
  pool: TypeIntensityPool
) {
  return useCallback(
    (options?: IFAnimateRecipeOptions) => {
      const intensity = options?.intensity ?? "medium";
      const list = pool[intensity];
      if (!list?.length) return;

      const name = list[Math.floor(Math.random() * list.length)];
      run(name, options);
    },
    [run, pool]
  );
}
