import React, {
  Children,
  cloneElement,
  isValidElement,
  useEffect,
  useState,
} from "react";
import type { AnimName } from "../key-frames";
import Animate from "./Animate";

export interface IFAnimateGroupProps {
  type: AnimName;
  stagger?: number;
  children: React.ReactNode;
  className?: string;
}

export const AnimateGroup: React.FC<IFAnimateGroupProps> = ({
  type,
  stagger = 80,
  children,
  className,
}) => {
  const [visibleCount, setVisibleCount] = useState(0);
  const items = Children.toArray(children);

  useEffect(() => {
    if (!stagger) {
      setVisibleCount(items.length);
      return;
    }

    let i = 0;
    const timer = setInterval(() => {
      i++;
      setVisibleCount(i);
      if (i >= items.length) clearInterval(timer);
    }, stagger);

    return () => clearInterval(timer);
  }, [stagger, items.length]);

  return (
    <div className={className}>
      {items.map((child, index) => {
        if (!isValidElement(child)) return child;
        if (index >= visibleCount) return null;

        return (
          <Animate key={index} type={type}>
            {cloneElement(child)}
          </Animate>
        );
      })}
    </div>
  );
};
