// Simple glowing pulse (box-shadow expand/contract)
const glow = `
  0% { box-shadow: 0 0 5px #ff00de; }
  50% { box-shadow: 0 0 20px #ff00de; }
  100% { box-shadow: 0 0 5px #ff00de; }
`;

// Neon text glow (flicker effect)
const glowTextFlicker = `
  0% { color: inherit; text-shadow: none; }
  2%, 59%, 64%, 79% { color: #fff; }
  3%, 59%, 63%, 78% {
    text-shadow:
      0px 0px 60px,
      0 0 22px,
      0 0 1em inherit,
      0 0 0.5em inherit,
      0 0 .1em inherit,
      0 10px 3px #000;
  }
  60%, 75% { color: inherit; text-shadow: none; }
`;

// Rainbow glowing (animated hue rotation)
const glowRainbow = `
  0% { filter: hue-rotate(0deg); }
  100% { filter: hue-rotate(360deg); }
`;

// Soft breathing glow
const glowBreathing = `
  0%, 100% { text-shadow: 0 0 5px #00f, 0 0 10px #00f; }
  50% { text-shadow: 0 0 20px #00f, 0 0 40px #00f; }
`;

// Glitch neon flicker (irregular intensity)
const glowGlitch = `
  0%, 100% { text-shadow: 0 0 5px #0ff, 0 0 10px #0ff; }
  10% { text-shadow: none; }
  20% { text-shadow: 0 0 15px #f0f, 0 0 30px #f0f; }
  30% { text-shadow: none; }
  40% { text-shadow: 0 0 25px #ff0, 0 0 50px #ff0; }
  60% { text-shadow: none; }
  80% { text-shadow: 0 0 15px #0ff, 0 0 35px #0ff; }
`;

// Map for usage
export const glowMap = {
  glow,
  glowTextFlicker,
  glowRainbow,
  glowBreathing,
  glowGlitch,
};
