// core/recipe.ts
import type { AnimName } from "../key-frames";
import type {
  IFAnimateRecipeOptions,
  TypeAnimateRecipeResolver,
} from "../animation/types";

export type TypeRecipeKey =
  | "idle"
  | "enter"
  | "exit"
  | "loading"
  | "success"
  | "error"
  | "attention";

export type TypeRecipeConfig = Partial<
  Record<TypeRecipeKey, TypeAnimateRecipeResolver>
>;

export function createRecipe(
  run: (name: AnimName, options?: IFAnimateRecipeOptions) => void,
  config: TypeRecipeConfig
) {
  const api: any = {};

  Object.keys(config).forEach((key) => {
    api[key] = (options?: IFAnimateRecipeOptions) => {
      const resolver = config[key as TypeRecipeKey];
      if (!resolver) return;

      const anim =
        typeof resolver === "function"
          ? resolver(options)
          : resolver;

      run(anim, options);
    };
  });

  api.run = (key: TypeRecipeKey, options?: IFAnimateRecipeOptions) => {
    const resolver = config[key];
    if (!resolver) return;

    const anim =
      typeof resolver === "function"
        ? resolver(options)
        : resolver;

    run(anim, options);
  };

  return api as Record<
    TypeRecipeKey,
    (options?: IFAnimateRecipeOptions) => void
  > & {
    run: (key: TypeRecipeKey, options?: IFAnimateRecipeOptions) => void;
  };
}
