import type { AnimName } from "../key-frames";
import { useAnimate } from "./useAnimate";

export function useFocusAnimate(options: {
  focus?: AnimName;
  blur?: AnimName;
}) {
  const anim = useAnimate<HTMLElement>();

  return {
    ...anim,
    bind: {
      ref: anim.ref,
      onFocus: () => options.focus && anim.play(options.focus),
      onBlur: () => options.blur && anim.play(options.blur),
    }
  };
}
