/* ===========================================================
   5. Fun / Attention Animations
=========================================================== */

/** Heartbeat animation */
const heartBeat = `
  0%, 28%, 70%, 100% { transform: scale(1); }
  14%, 42% { transform: scale(1.3); }
`;

/** Tada animation (shake + scale) */
const tada = `
  from { transform: scale3d(1, 1, 1); }
  10%, 20% { transform: scale3d(.9, .9, .9) rotate(-3deg); }
  30%, 50%, 70%, 90% { transform: scale3d(1.1, 1.1, 1.1) rotate(3deg); }
  40%, 60%, 80% { transform: scale3d(1.1, 1.1, 1.1) rotate(-3deg); }
  to { transform: scale3d(1, 1, 1); }
`;

/** Hinge animation (falls down like hinge) */
const hinge = `
  0% { transform: rotate(0); transform-origin: top left; animation-timing-function: ease-in-out; }
  20%, 60% { transform: rotate(80deg); transform-origin: top left; animation-timing-function: ease-in-out; }
  40%, 80% { transform: rotate(60deg); transform-origin: top left; animation-timing-function: ease-in-out; }
  100% { transform: translateY(700px); opacity: 0; }
`;

/** LightSpeedIn from left */
const lightSpeedInLeft = `
  from { transform: translateX(-100%) skewX(30deg); opacity: 0; }
  60% { transform: skewX(-20deg); opacity: 1; }
  80% { transform: skewX(5deg); }
  to { transform: none; }
`;

/** LightSpeedOut to right */
const lightSpeedOutRight = `
  from { opacity: 1; }
  to { transform: translateX(100%) skewX(-30deg); opacity: 0; }
`;

/** ===========================================================
 * Fun / Attention Map
=========================================================== */
export const funMap = {
  heartBeat,
  tada,
  hinge,
  lightSpeedInLeft,
  lightSpeedOutRight,
};
