/* ===========================================================
   Zoom Animations
=========================================================== */

/** Simple zoom in (center) */
const zoomIn = `
  from { transform: scale(2); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
`;

/** Simple zoom out (center) */
const zoomOut = `
  from { transform: scale(1); opacity: 1; }
  to { transform: scale(2); opacity: 0; }
`;

/** Zoom in from left */
const zoomInFromLeft = `
  from { transform: scale(2) translateX(-100%); opacity: 0; }
  to { transform: scale(1) translateX(0); opacity: 1; }
`;

/** Zoom in from right */
const zoomInFromRight = `
  from { transform: scale(2) translateX(100%); opacity: 0; }
  to { transform: scale(1) translateX(0); opacity: 1; }
`;

/** Zoom in from top */
const zoomInFromTop = `
  from { transform: scale(2) translateY(-100%); opacity: 0; }
  to { transform: scale(1) translateY(0); opacity: 1; }
`;

/** Zoom in from bottom */
const zoomInFromBottom = `
  from { transform: scale(2) translateY(100%); opacity: 0; }
  to { transform: scale(1) translateY(0); opacity: 1; }
`;

/** Zoom out to left */
const zoomOutToLeft = `
  from { transform: scale(1) translateX(0); opacity: 1; }
  to { transform: scale(2) translateX(-100%); opacity: 0; }
`;

/** Zoom out to right */
const zoomOutToRight = `
  from { transform: scale(1) translateX(0); opacity: 1; }
  to { transform: scale(2) translateX(100%); opacity: 0; }
`;

/** Zoom out to top */
const zoomOutToTop = `
  from { transform: scale(1) translateY(0); opacity: 1; }
  to { transform: scale(2) translateY(-100%); opacity: 0; }
`;

/** Zoom out to bottom */
const zoomOutToBottom = `
  from { transform: scale(1) translateY(0); opacity: 1; }
  to { transform: scale(2) translateY(100%); opacity: 0; }
`;

/** Zoom in rotate */
const zoomInRotate = `
  from { transform: scale(0) rotate(-180deg); opacity: 0; }
  to { transform: scale(1) rotate(0deg); opacity: 1; }
`;

/** Zoom out rotate */
const zoomOutRotate = `
  from { transform: scale(1) rotate(0deg); opacity: 1; }
  to { transform: scale(0) rotate(180deg); opacity: 0; }
`;

/** Zoom in flip (3D X-axis) */
const zoomInFlipX = `
  from { transform: perspective(400px) scale(0.5) rotateX(90deg); opacity: 0; }
  to { transform: perspective(400px) scale(1) rotateX(0deg); opacity: 1; }
`;

/** Zoom out flip (3D X-axis) */
const zoomOutFlipX = `
  from { transform: perspective(400px) scale(1) rotateX(0deg); opacity: 1; }
  to { transform: perspective(400px) scale(0.5) rotateX(90deg); opacity: 0; }
`;

/** Zoom in flip (3D Y-axis) */
const zoomInFlipY = `
  from { transform: perspective(400px) scale(0.5) rotateY(90deg); opacity: 0; }
  to { transform: perspective(400px) scale(1) rotateY(0deg); opacity: 1; }
`;

/** Zoom out flip (3D Y-axis) */
const zoomOutFlipY = `
  from { transform: perspective(400px) scale(1) rotateY(0deg); opacity: 1; }
  to { transform: perspective(400px) scale(0.5) rotateY(90deg); opacity: 0; }
`;

/** Zoom pulse (in-out loop) */
const zoomPulse = `
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
`;

/** Zoom swing (elastic effect) */
const zoomSwing = `
  0% { transform: scale(0.3); opacity: 0; }
  50% { transform: scale(1.2); opacity: 1; }
  70% { transform: scale(0.9); }
  100% { transform: scale(1); }
`;

/* ===========================================================
 * Zoom Animation Map
=========================================================== */
export const zoomMap = {
  zoomIn,
  zoomOut,
  zoomInFromLeft,
  zoomInFromRight,
  zoomInFromTop,
  zoomInFromBottom,
  zoomOutToLeft,
  zoomOutToRight,
  zoomOutToTop,
  zoomOutToBottom,

  // Extra
  zoomInRotate,
  zoomOutRotate,
  zoomInFlipX,
  zoomOutFlipX,
  zoomInFlipY,
  zoomOutFlipY,
  zoomPulse,
  zoomSwing,
};
